.section3 {
    background-color: white;
    min-height: 100vh;
    .section_heding{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-top: 5rem;
         h3 {
            text-align: center;
            font-size: 2.5rem;
            font-weight: 600;
            margin-top: 2rem;
        }
         p {
            text-align: center;
            font-size: 2rem;
            font-weight: 500;
            padding: 2rem;
        }
    }
    .boxcontainer {

        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        gap: 1rem;
        .box {
            flex: 1 1 300px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            
             .img {
                width: 50%;
                object-fit: cover;
            }
             h5 {
                font-size: 2rem;
                font-weight: 700;
                margin-top: 1rem;
            }
            p {
                font-size: 1.3rem;
                padding: 0 8rem;
                font-weight: 400;
            }
        }
    }
    
}