#footer {
    display: flex;
    background-color: #161418;
    color: white;
    width: 100%;
    height: 100vh;

    .footer {


        .footer_content {
            padding: 3rem 0rem 0rem 1rem;

            h5 {
                font-size: 2rem;
                text-transform: uppercase;
                line-height: 3rem;
                font-weight: 700;

                span {

                    color: #fa7c30;
                }
            }

            p {
                width: 40rem;
                height: 8rem;
                font-size: 1.4rem;
                line-height: 3rem;
                color: #ffffff;
                font-weight: 40rem;
                margin-top: 2.9rem;
            }


            .socialicon {
                font-size: 1.5rem;
                color: red;

                margin-top: 2.9rem;

                span {
                    color: white;
                }

                i {
                    padding-right: 2rem;

                    &:hover {
                        transform: scale(2, 2);
                    }
                }



            }

            .subscribe_btn input {
                width: 20rem;
                height: 3.5rem;

                margin-top: 1.5rem;
                border-radius: 0.5rem;


            }

            .subbtn {
                width: 10rem;
                height: 3.5rem;
                background-color: #fa7c30;
                margin-top: 1.5rem;
                border-radius: 0px 5px 5px 0px;
                position: relative;
                right: 2rem;

                &:hover {
                    background-color: red;
                }

            }




        }
    }

    .footer-content2 {
        margin-top: 2.5rem;
        padding: 1rem;

        h5 {
            font-size: 2rem;
            text-transform: uppercase;
            line-height: 3rem;
            font-weight: 700;

        }

        .box1 {
            display: flex;
            text-align: center;
            justify-content: center;
            margin-top: 1rem;

            img {
                margin-top: 1.5rem;
            }

            p {
                font-size: 1.4rem;
                line-height: 3rem;
                color: #ffffff;
                font-weight: 400;
            }
        }

    }

    .content3 {

        padding: 3rem 1rem 0rem 0rem;

        h5 {
            font-size: 20px;
            text-transform: uppercase;
            line-height: 30px;
            font-weight: 700;
        }

        img {
            margin-top: 2.5rem;
        }

        a {
            font-size: 1.4rem;
            line-height: 3rem;
            color: red;
            font-weight: 400;

            &:hover {
                color: white;
            }
        }




    }
}