
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    left: 0;
    right: 0;
    padding: 0.5rem 5%;
    background: rgba(0, 0, 0, 0.6);



    #logo {
        font-size: 2.5rem;
        color: white;
        margin-right: 5rem;
        font-weight: bold;

        span {
            color: red;
        }
    }


    .navbar {




        .menu {
            color: white;
            padding-right: 5rem;
            font-size: 1.5rem;
            font-weight: 40rem;


            &:hover {
                color: red;
            }
        }




    }

    .nav-btn {
        display: none;
        cursor: pointer;
        padding: 5px;
        background: transparent;
        border: none;
        color: white;
        opacity: 0;
        font-size: 10px;
    }

}